.intro {
  display: flex;
  height: 100vh;
}

.intro-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.intro-right {
  flex: 1;
  position: relative;
}

.intro-left-wrapper {
  padding: 50px;
  height: 75%;
  /* background-color: red; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.intro-start {
  font-size: 30px;
  font-weight: 300;
}

.intro-name {
  font-size: 60px;
}

.intro-title {
  height: 50px;
  align-items: center;
  overflow: hidden;
}

.intro-title-wrapper {
  height: 100%;
  animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
  25% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(-100px);
  }
  75% {
    transform: translateY(-150px);
  }
  100% {
    transform: translateY(-200px);
  }
}

.intro-title-item {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: rgb(43, 183, 226);
  display: flex;
  align-items: center;
}

.intro-description {
  line-height: 1.6;
}

.intro-scroll {
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 20px;
}

.intro-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
}

.intro-bg {
  clip-path: polygon(100% 0%, 100% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
  background-color: rgb(43, 183, 226);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
}

@media screen and (max-width: 480px) {
  .intro {
    height: 150vh;
    flex-direction: column;
  }

  .intro-left-wrapper {
    padding: 20px;
    line-height: 1.6;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100%;
  }

  .intro-description {
    align-items: center;
  }

  .intro-scroll {
    display: none;
  }
}
